import React from 'react';
import { Button, Modal, Form, InputGroup, Col } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';


class COASubCategoryModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} COASubCategory
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="COASubCategoryCode">
                                    <Form.Label>COA Sub Category Code</Form.Label>
                                    <Form.Control type="text" placeholder="Enter COA Sub Category Code"
                                        value={this.props.COASUBCATEGORY_CODE} required
                                        onChange={this.props.changeCOASubCategoryCode}
                                        name="COASubCategoryCode" />
                                    <Form.Control.Feedback type="invalid">
                                        Please select COA Sub Category Code.
                                 </Form.Control.Feedback>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="COADescription">
                                    <Form.Label>Chart of Account</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.COA_DESCRIPTION}
                                            placeholder="Search Chart of Account"
                                            aria-describedby="inputGroupPrepend"
                                            name="COADescription"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleCOASearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select COA Description.
                                 </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="COACategoryCode">
                                    <Form.Label>COA Category</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.COACATEGORY_CODE}
                                            placeholder="Search COA Category"
                                            aria-describedby="inputGroupPrepend"
                                            name="COACategoryCode"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleCOACategorySearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select COA Category Code.
                                 </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                                <Form.Group controlId="SystemStatusType">
                                    <Form.Label>System Status Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            name="SystemStatus"
                                            value={this.props.SystemStatusTypeDescription}
                                            placeholder="Search System Status Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select status.
                                 </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete COA Sub Category
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button"
                                    value={this.props.COASUBCATEGORY_CODE} onChange={this.props.COASUBCATEGORY_CODE} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default COASubCategoryModal;